<script>


export default {
    name: "Client",
    components: {

    },
    data() {
        return {
            screenWidth: 0,
            size: 0
        };
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth();
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth
        },
        setSize() {
            if (this.screenWidth >= '992')
                this.size = 4
            else
                this.size = 2
        },
    },
    created() {
        this.updateScreenWidth()
        this.onScreenResize()
        this.setSize()
        // console.log(this.size)
    }
};
</script>

