<template>
    <section id="partner" class="partner">
        <div class="container">
            <h3 class="partner-title">
                Our Partners
            </h3>
            <div class="content">
                <div class="row" style="row-gap: 50px;">
                    <div class="col-lg-3 col-sm-6" v-for="(item, index) in $store.state.partner_data" :key="index">
                        <img :src="item.image" class="partner-logo" :alt="item.name" />
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>


export default {
    name: "Partner",
    components: {

    },
    data() {
        return {
            screenWidth: 0,
            size: 0,
        };
    },
    methods: {
        onScreenResize() {
            window.addEventListener("resize", () => {
                this.updateScreenWidth();
            });
        },
        updateScreenWidth() {
            this.screenWidth = window.innerWidth
        },
        setSize() {
            if (this.screenWidth >= '992')
                this.size = 3
            else
                this.size = 1
        },
        
    },

    created(){
        this.updateScreenWidth()
        this.onScreenResize()
        this.setSize()
    },
};
</script>

<style lang="scss">
.partner {
    padding: 60px 100px 80px;
    background-color: rgba(231, 231, 231, 0.356);
}

.partner .partner-title {
    text-align: center;
    font-size: 35px;
    font-weight: bold;
    color: var(--main-blue);
    margin-bottom: 60px;
}

